/*------------------------------------*\
    部落格內文設定 */
/*------------------------------------*\ */

.blog {

    &-article {

        &-header {
            display: flex;
            width: 70%;
            @include respond-to('sphone') {
                width: 100%; } }

        &-footer {
            width: 70%;

            @include respond-to('phone') {
                width: 100%; }
            @include respond-to('phone') {
                display: none; } }

        &-content {
            margin-right: 24px;
            @include respond-to('phone') {
                margin-right: 0; } }

        &-tag {
            margin-bottom: 8px;
            display: flex;

            &Word {
                @include fontSize(24px,36px,bold);
                color: $color-primary;
                margin-right: 15px;
                @include respond-to('phone') {
                    @include fontSize(16px,24px); } } }

        &-title {
            @include fontSize(48px,72px);
            @include respond-to('phone') {
                @include fontSize(24px,36px); } }

        &-subtitle {
            @include fontSize(24px,36px);
            margin-bottom: 16px;
            @include respond-to('phone') {
                @include fontSize(16px,24px); } }

        &-img {
            @include size(100%,520px);
            object-fit: cover;
            margin-bottom: 8px;
            @include respond-to('phone') {
                @include size(100%,320px); }
            @include respond-to('phone') {
                @include size(100%,208px); } }

        &-word {
            @include fontSize(16px,24px);
            color: #555555;
            margin-bottom: 40px;
            @include respond-to('phone') {
                margin-bottom: 16px; } } }

    &-share {
        @include flexGrid(flex-start);
        padding-bottom: 16px;
        margin-bottom: 24px;
        border-bottom: 1px solid $color-black;

        &-word {
            margin-right: 16px; }

        &-list {
            display: flex; }

        &-icon {
            @include size(24px);
            margin-right: 16px; } }

    &-page {

        &-btns {
            @include flexGrid(space-between);
            width: 100%; }

        &-prev {
            transform: rotate(180deg); }

        &-link {
            @include flexGrid;
            color: $color-black;

            >.blog-page-arrow {
                margin-right: 16px;
                margin-left: 0; }

            &.reverse {
                flex-direction: row-reverse;

                >.blog-page-arrow {
                    margin-right: 0;
                    margin-left: 16px; } } }

        &-arrow {
            @include size(16px); } }

    &-tag {

        &-menu {
            width: 28%;
            @include respond-to('sphone') {
                display: none; } }

        &-icon {
            margin-right: 12px;
            @include respond-to('phone') {
                @include size(auto,24px); } }

        &-title {
            @include flexGrid(flex-start);
            margin-bottom: 4px; }

        &-word {
            @include fontSize(32px,48px,bold);
            color: $color-primary;
            @include respond-to('phone') {
                @include fontSize(16px,24px,bold); } }


        &-item {
            @include fontSize(24px,36px,bold);
            color: $color-primary;
            display: inline-block;
            margin: 0 24px 4px 0;
            @include respond-to('phone') {
                @include fontSize(16px,24px,bold); } } } }
