/*------------------------------------*\
    部落格文章列表樣式 */
/*------------------------------------*\ */

.blog-list {

    &-item {
        display: flex;
        margin-bottom: 60px;
        @include respond-to('phone') {
            margin-bottom: 24px; }

        &:last-child {
            margin-bottom: 124px;
            @include respond-to('phone') {
                margin-bottom: 80px; }
            @include respond-to('sphone') {
                margin-bottom: 64px; } }

        @include respond-to('sphone') {
            flex-direction: column; } }

    &-img {
        @include size(50%,400px);
        margin-right: 24px;

        @include respond-to('phone') {
            @include size(50%,208px); }
        @include respond-to('sphone') {
            @include size(100%,208px); } }

    &-content {
        @include flexGrid(space-between,flex-end);
        flex-direction: column;
        width: 50%;
        @include respond-to('sphone') {
            width: 100%;
            flex-direction: row; } }

    &-title {
        @include fontSize(32px,48px);
        color: $color-third;

        @include respond-to('phone') {
            @include fontSize(24px,36px); } }

    &-info {
        @include flexGrid(space-between);
        margin-bottom: 16px;
        @include respond-to('phone') {
            margin-bottom: 4px; } }

    &-subtitle {
        @include fontSize(20px,30px);

        @include respond-to('phone') {
            @include fontSize(16px,24px); } }

    &-date {
        @include fontSize(16px,24px); }

    &-word {
        @include fontSize(20px,30px);
        color: #555555;
        @include respond-to('phone') {
            @include fontSize(16px,24px); }
        @include respond-to('sphone') {
            display: none; } }

    &-text {
        @include respond-to('sphone') {
            width: 100%; } }

    &-link {

        &.md {
            display: none;
            color: #555555;
            @include respond-to('phone') {
                display: inline-block; }

            &:hover {
                color: $color-primary; } }

        &.lg {
            @include fontSize(36px,54px,bold);
            color: $color-third;
            transition: .3s;

            &:hover {
                color: $color-primary; }

            @include respond-to('phone') {
                display: none; } } } }
