/*------------------------------------*\
    門市據點列表 */
/*------------------------------------*\ */

.store {

    &-list {
        display: flex;
        flex-wrap: wrap; }

    &-card {

        &-introduction {
            width: 30%;
            margin-right: 24px;
            &:nth-child(3n) {
                margin: 0 0 48px 0; }

            @include respond-to('phone') {
                width: 48%;
                &:nth-child(3n) {
                    margin-right: 24px; }
                &:nth-child(2n) {
                    margin: 0 0 48px 0; } }

            @include respond-to('sphone') {
                width: 100%;
                margin-right: 0;
                margin-bottom: 24px;
                &:nth-child(3n),
                &:nth-child(2n) {
                    margin-right: 0;
                    margin-bottom: 24px; } } }

        &-detail {
            width: 40%;
            margin-right: 24px;

            @include respond-to('phone') {
                width: 100%;
                display: flex;
                margin: 0 0 24px 0; }

            >.store-card-img {
                width: 50%;
                margin-right: 24px; }

            >.store-card-content {
                width: 50%; }

            @include respond-to('sphone') {
                flex-direction: column;
                >.store-card-img {
                    width: 100%;
                    margin-right: 0; }

                >.store-card-content {
                    width: 100%; } } }

        &-info {
            display: flex;
            margin-bottom: 8px;
            &:last-child {
                margin-bottom: 0px; } }

        &-img {
            height: 224px; }

        &-title {
            @include fontSize(24px, 36px);
            padding: 8px 24px;
            border: 1px solid $color-gary; }

        &-list {
            @include fontSize(20px, 30px);
            padding: 16px 24px;
            border: 1px solid $color-gary; }

        &-icon {
            @include size(24px);
            margin-right: 10px; }

        &-btn {
            @include fontSize(19px, 27px);
            display: inline-block;
            width: 100%;
            text-align: center;
            padding: 16px 0;
            background-color: $color-black;
            color: $color-white;
            transition: .3s;

            @extend .border-none;
            @extend .hover-btn-animation; } } }
