/*------------------------------------*\
    搜尋欄導覽列設定 */
/*------------------------------------*\ */

.search {

    &-menu {
        background-color: $color-third;

        >.container {
            @include flexGrid(space-between); } }

    &-navbar {
        display: flex;

        @include respond-to('sphone') {
            @include flexGrid(space-between);
            width: 100%; }

        &-link {
            @include fontSize(16px,24px);
            display: inline-block;
            padding: 13px 0;
            margin-right: 48px;
            color: $color-white;
            transition: .3s;
            &:hover {
                color: #ffba08; }

            @include respond-to('sphone') {
                margin-right: 0; } }

        &-icon {
            @include respond-to('sphone') {
                display: none; } } } }
